@import "./module/vars";
@import "./module/comm";

html,body{
    font-size:14px;
}
.wrapper{
    margin-left: auto;
    margin-right: auto;
    
    
    @media (min-width: $breakpoint-lg) {
		max-width: $lg-page-width;
	}
    /* @media (min-width: $breakpoint-lg) and (max-width: $breakpoint-xl){
        background-color: $pink;
		max-width: $lg-page-width;
	} */
    @media (min-width: $breakpoint-xs) and (max-width: $breakpoint-lg){
		max-width: $md-page-width;
	}
}
.tr {
    display: table-row;
}
.td {
    display: table-cell;
    padding: 5px;
    border: 1px solid #ddd;
    border-collapse: collapse;
}
.subTitle{
    position: relative;
    margin-top: -2.5em;
    line-height: 2.5em;
    background-color: rgba(255, 255, 255, .7);
    color: $black;
}

